.JoinPage{
background-color: rgb(31, 29, 29);
width:100vw;
height:100vh;
display: flex;
justify-content: center;
align-items: center;
}





.JoinContainer{
    border :2px solid white;
    height:55%;
    width:50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2%;

}

.JoinContainer > h1{
    font-size: 2.5vmax;
    color: white;
    width:25vmax;
    border-bottom: 0.2vmax solid white;
    text-align: center;
    padding: 0.5vmax;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-sizing: border-box;
}


#joinInput{
    width:25vmax;
    height:3vmax;
    padding:2vmax;
    border: none;
    outline: none;
    font-size: 1.2vmax;
    margin: 2vmax;
    box-sizing: border-box;
}

.joinbtn{
    width:20vmax;
height: 4vmax;
    padding: 1.5vmax;
    border:none;
    outline: none;
    font-size: 1.2vmax;
    background-color:rgb(38, 178, 38);
    border-radius: 60%;
    transition: all 0.3s;
}

.joinbtn:hover{
    background-color: darkgreen;
    cursor: pointer;
}


