.chatPage{
    background-color: rgb(31, 29, 29);
  
width:100vw;
height:100vh;
display: flex;
justify-content: center;
align-items: center;
}

.chatContainer{
    background-color: white;
    height:70%;
    width:60%;
}

.header{
    background-color: crimson;
    height:15%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header h2{
    color: white;
    margin: 1.5vmax;
}

.header img{
    cursor: pointer;
width: 6vmax;
margin: 1.5vmax;
transition: all 0.3s;
}

.header img :hover{
    transform: scale(1.2);
}



.chatBox{
    border-radius: 0.2vma solid white;
    height: 74%;
    box-sizing: border-box;
    border-radius: 20px;

}

.inputBox{
border: none;
    height:11%;
    box-sizing: border-box;
    display: flex;
justify-content: space-between;
background-color: rgb(31, 29, 29);
padding-top: 5px;

}

#chatInput{
    width:90%;
    border:none;
padding-left: 2vmax;
box-sizing: border-box;
    background-color: white !important ;
    outline: none;
    font-size: 1.8vmax;
    font-weight: 550;
    font-family:Verdana, Geneva, Tahoma, sans-serif;
    background-color:rgba(36, 32, 32, 0.675);
    border-radius: 20px 20px 20px 20px ;
    
}

.sendbtn{
    background-color: rgb(37, 186, 37);
    color:white;
    border:none;
    width:10%;
    transform: all 0.3s;
    border-radius: 50%;
  
}
.sendbtn img{
width:3vmax;
filter: brightness(100);
transition: all 0.3s;

}

.sendbtn:hover {
    background-color:darkgreen;
    cursor: pointer;
}


.sendbtn:hover img{
    transform: translate(10px);
}









