.messageBox{
  
    width:50%;
    padding: 1vmax;
    margin: 1.8vmax;
    border-radius: 0.5vmax;
    display: inline-block;
    clear: both;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
}


.right{
    float: right;
    background-color: crimson;
    color:white;

}


.left{
    float: left;
    background-color:rgb(184,255,194);

    
}